import './ContentRatings.css';
import { animated, useSpring } from '@react-spring/web';
import MyNavbar from '../MyNavbar';
import { Row, Col, Container } from 'react-bootstrap';
import { useEffect } from 'react';


function ContentRatings() {
  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="ContentRatings">
      <MyNavbar active='content_ratings' />
      <animated.div style={fadein_props}>
        <h1 className='Title'>content ratings</h1>

        <p id='introParagraph'>
          Content ratings are present on my content to indicate the approximate age range at
          which a particular content may be suitable or yet too inappropriate for minors of
          a certain age range.
        </p>
        <p>
          The following quick guidelines may not always be applied or interpreted in the most literal
          manner, as the overall context, tone, and psychological impact are important considerations
          for content rating classification. Flexibility in approach of content rating classification
          will be exercised.
        </p>

        <br />

        {/* <Button href="https://docs.google.com/document/d/1G38yr6od2fvqcpDMNzFjbzEgobzpzIzCduvSPjU_CdQ/edit?usp=sharing"
          target="_blank" rel="noopener noreferrer" id='contentRatingGuidelinesButton'>
          CONTENT RATING GUIDELINES
        </Button>
        <br /> */}

        <Container>
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/00G.png')} alt='G' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h5><u>Generally suitable for viewing by all ages.</u></h5>
              <ul>
                <li>A few uses of mildest language (e.g., bloody, crap, damn, god, hell).</li>
                <li>Occasional brief nonsexual nudity with contextual justificaiton. Mild behaviour such as embracing, kissing, etc.</li>
                <li>Some mild animated, comic, fantastic violence and short scary scenes with reassuring outcomes.</li>
                <li>Drinking and smoking by adults as background detail. Drug use and underage drinking/smoking only in educational content.</li>
                <li>Family-friendly themes sensitive to young children's sense of wellbeing and security. Mild crude content/humour.</li>
              </ul>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/09+.png')} alt='9+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h5><u>May not be suitable for viewing by children under 9.</u></h5>
              <ul>
                <li>Some mild language (e.g., ass, asshole, balls, bastard, effing, goddamn, piss, shit).</li>
                <li>Brief nonsexual nudity with contextual justificaiton. Mild suggestive content and innuendoes.</li>
                <li>Stronger violence, especially if comic or fantastic, and more frightening scenes. Brief mild blood/injury detail.</li>
                <li>Alcohol and tobacco not glamorized. A few mild age-appropriate references to drug use and underage drinking/smoking.</li>
                <li>Some serious real-life issues (e.g., cyberbullying, discrimination, abuse, depression). Some dangerous imitable behaviour.</li>
              </ul>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/12+.png')} alt='12+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h5><u>Not recommended for viewing by children under 12.</u></h5>
              <ul>
                <li>Language may include some uses of "f**k" (not sexual or aggressive). A few discriminatory slurs with contextual justification. </li>
                <li>Nonsexual nudity not gratuitous or very crude. Some sexual content and references.</li>
                <li>Violence and horror with some blood or injury detail.</li>
                <li>A few depictions of drug use or underage drinking/smoking with contextual justification.</li>
                <li>Mature themes (e.g., drug abuse, sexual harassment, politics, suicide, controversial issues). Dangerous behaviour.</li>
              </ul>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/15+.png')} alt='15+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h5><u>Not recommended for viewing by minors under 15.</u></h5>
              <ul>
                <li>Strong coarse language, which may include some discriminatory language.</li>
                <li>Sexual activity and nudity not graphic or prolonged. Some strong sexual references.</li>
                <li>Intense violence and horror with some gore and sadism/torture. Some brief discreet depictions of sexual violence.</li>
                <li>Drug use, substance abuse, underage drinking/smoking.</li>
                <li>No limits on theme. Dangerous, antisocial (harmful to society), or criminal behaviour not encouraged.</li>
              </ul>
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/18+.png')} alt='18+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h5><u>Suitable for viewing by adults 18 and up only.</u></h5>
              <ul>
                <li>Normalized discriminatory language.</li>
                <li>Gratuitous or very crude nudity. Explicit sexual content. Any content intended to sexually arouse.</li>
                <li>Strong focus on explicit detail of inflicting violence, heavy gore. Graphic or sustained sexual violence, suicide, self-harm.</li>
                <li>Glamorized or very detailed (i.e., containing instructional detail) drug use.</li>
                <li>Dangerous, antisocial (harmful to society) behaviour not promoted overall.</li>
              </ul>
            </Col>
          </Row><br />
        </Container>
      </animated.div>
    </div>
  );
}

export default ContentRatings;
